import { Component, h, Element } from '@stencil/core/internal';
import tailwind from '../../output.css';
import { data } from '../../data';
@Component({
  tag: 'rf-photo',
  shadow: true,
})
export class Photo {
  @Element() element: HTMLElement;

  images = data.My_Result.images;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  render() {
    return (
      <div class="flex flex-row gap-2 mt-20 mb-10">
        <div class="w-full md:w-1/2 h-full">
          <img src={this.images[0].url} class="rounded-2xl object-cover w-full h-full"></img>
        </div>
        <div class="w-1/2 hidden md:grid grid-cols-2 gap-2 h-full">
          <img src={this.images[1].url} class="rounded-2xl object-cover w-full h-26"></img>
          <img src={this.images[2].url} class="rounded-2xl object-cover w-full h-26"></img>

          <img src={this.images[3].url} class="rounded-2xl object-cover w-full h-26"></img>
          <img src={this.images[4].url} class="rounded-2xl object-cover w-full h-26"></img>
        </div>
      </div>
    );
  }
}
