import { Component, h, Element, Event, EventEmitter, State } from '@stencil/core/internal';
import tailwind from '../../output.css';
import { data } from '../../data';
import { io } from 'socket.io-client';
@Component({
  tag: 'rf-rooms',
  shadow: true,
})
export class Rooms {
  @Element() element: HTMLElement;

  @State()roy = true
  @State() rooms = [...data.My_Result.roomtypes]
  amenities = data.My_Result.amenities;

  socket: any;
  componentWillLoad() {
    this.socket = io('http://localhost:3000');
    this.socket.on('connection', () => {
      console.log('Connected to server');
    });
    this.socket.on('disable_room_type', room_id => {
      this.handleDisableRoomType(room_id)
    });
    this.socket.on('activate_room_type', room_id => {
      this.handleActivateRoomType(room_id)
    });
    
  }
  handleDisableRoomType(room_id) {
    let updatedRooms = this.rooms.map(item => {
      if (item.id === room_id) {
        return { ...item, is_active: false };
      }
      return item;
    });
    this.rooms = [...updatedRooms]
  }

  handleActivateRoomType(room_id) {
    let updatedRooms = this.rooms.map(item => {
      if (item.id === room_id) {
        return { ...item, is_active: true };
      }
      return item;
    });
    this.rooms = [...updatedRooms]
  }

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  @Event({ bubbles: true, composed: true }) rf_sendId: EventEmitter<number>;
  sendId(id: number) {
    console.log(id);
    this.rf_sendId.emit(id);
  }
  render() {
    return (
      <div>
        {this.rooms.map((e, i) => {
          if (e.is_active) {
            return (
              <div class="flex flex-col md:flex-row bg-gray-200 rounded-lg my-4 p-4 gap-4">
                <div class="rounded-lg overflow-hidden w-full md:w-1/4 h-72 md:h-48 relative hover:cursor-pointer">
                  <rf-carousel
                    images={e.images.map(i => {
                      return i.url;
                    })}
                    roomId={i}
                  ></rf-carousel>

                  <div class="absolute bottom-0 flex flex-row w-full justify-between px-4 bg-gray-400 bg-opacity-70" onClick={this.sendId.bind(this, i)}>
                    <div>Max {e.occupancy_max.adult_nbr}</div>
                    <div>
                      {e.size}m<sup>2</sup>
                    </div>
                    <div>...more</div>
                  </div>
                </div>
                <div class="w-full md:w-3/4 flex flex-col gap-y-2">
                  <h3 class="text-2xl font-semibold">{e.name}</h3>
                  <div class="flex flex-row gap-x-4">
                    {e.bedding_setup.map(b => {
                      return <div class="text-sm">{b.name}</div>;
                    })}
                  </div>
                  <div class="flex flex-row gap-x-4 text-sm">
                    <div>{this.amenities[2].description}</div>
                    <div>{this.amenities[14].description}</div>
                    <div>{this.amenities[25].description}</div>
                  </div>
                  <div class="text-sm">{e.description}</div>
                </div>
              </div>
            );
          }
        })}
      </div>
    );
  }
}
